@mixin Perfil() {
  .perfil_ {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0.72em 5px;
    border-bottom: 2px solid #2f3136;
    position: sticky;
    z-index: 999999;
    background-color: #202225;
    width: 100%;
    top: 0;
    
    div.photo{
      position: relative;
  
      &:before {
        content: "";
        width: 10px;
        height: 10px;
        background-color: greenyellow;
        position: absolute;
        left: 0;
        right: -39px;
        margin: 0 auto;
        top: 4px;
        border-radius: 50%;
      }
  
      img {
        border-radius: 50%;
      }
    }
  
    .information_ {
      padding: 10px;
      color: #efefef;
  
      .status {
        position: relative;
        display: flex;
        font-size: 10px;
        color:#545558;
      }
    }
  }
}