@import './HeaderMessage.scss';
@import './Perfil.scss';
@import './Users.scss';

*, button, input {
  font-family: 'Roboto', sans-serif;
}

html, body, #root {
  height: 100%;
}

::-webkit-scrollbar{
  width: 5px;
}

::-webkit-scrollbar-thumb{
  background-color: #202225;
}

::-webkit-scrollbar-track{
  border-radius: 5px;
  background-color: #4a4b4c;
}

.row {
  height: 100vh;

  > div {

    height: 100vh;
    overflow-y: auto;
    
    &:nth-child(1) {
      background-color: #202225;

      @include Perfil;

      @include Users;
    }

    &:nth-child(2) {
      background-color: #2f3136;
      overflow-y: scroll;
      height: 100vh;
      display: flex;
      flex-direction: column;

      @include HeaderMessage;
    }

    &:nth-child(3) {
      background-color: #36393f;
    }
  }
}