@mixin HeaderMessage {
  .type_message {
    .targetUser{
      display: flex;
      align-items: center;
      border-bottom: 2px solid #202225;
      padding: 1em 1em;
    
      .photo_ {
        position: relative;
  
        img {
          transition: ease-in 200ms;
          border-radius: 50%;
          width: 50px;
          height: 50px;
        }
      }
  
      .information {
        color: #efefef;
        padding: 0 10px;
  
        .title_ {
          font-size: 14px;
          font-weight: 900;
        }

        .description_ {
          font-size: 12px;
        }

        .status {
          font-size: 10px;
        }
      }
    }
  }
}