@mixin Users() {

  .list_{

    .user_{
      cursor: pointer;
      position: relative;
      display: flex;
      flex-direction: row;
      padding: 10px 5px;
      transition: ease-in 200ms;

      &:before {
        content:"";
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        right: 0;
        z-index: 1;
      }

      &:hover {
        background-color: #2f3136;

        > .photo_ {
          .notifications {

            &:before {
              border: 2px solid #2f3136;
            }
          }

          img {
            transition: ease-in-out 200ms;
            border-radius: 10px !important;
          }
        }

      }

      .photo_ {
        position: relative;

        .notifications {
          &:before {
            content: "99+";
            width: 25px;
            height: 25px;
            background-color: #bd2130;
            border: 2px solid #202225;
            position: absolute;
            left: 0;
            right: -40px;
            margin: 0 auto;
            top: 0px;
            border-radius: 50%;
            font-size: 10px;
            color: #fff;
            text-align: center;
            line-height: 22px;
            font-weight: 800;
          }
        }

        img {
          transition: ease-in 200ms;
          border-radius: 50%;
          width: 50px;
          height: 50px;
        }
      }

      .information_ {
        display: flex;
        flex-flow: column;
        justify-self: flex-start;
        justify-content: center;

        .title_ {
          color: #efefef;
          padding: 0 10px;
          font-size: 14px;
          font-weight: 900;
        }

        .description_ {
          color: #efefef;
          padding: 0 10px;
          font-size: 11px;
        }
      }
    }
  }
}